/**
 * @file  trim 
 */

import * as is from '../util/is'
import * as array from '../util/array'

/**
 *  trim 
 * 
 * @param str 
 * @param sep 
 */
export default function split(str: string | number, sep: string): string[] {

  const result = []

  if (is.number(str)) {
    str = str + ''
  }

  if (str && is.string(str)) {
    array.each(str.split(sep), (part, index) => {
      part = part.trim()
      if (part) {
        result.push(part)
      }
    })
  }

  return result
}
